body {
  margin: 0;
  padding: 0;
  color: var(--color-a);
  background-color: var(--bg-b);
  width: 100%;
  position: relative;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  min-height: -webkit-fill-available;
  -webkit-text-size-adjust: 100%;
  display: flex;
  flex-direction: column;
}

footer {
  flex: 0 0 auto;
}

html {
  box-sizing: border-box;
  width: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  min-height: -webkit-fill-available;
  scroll-behavior: smooth;
}


.site-content {
  background-color: var(--bg-b);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

main {
  flex: 1 0 auto;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}


picture {
  display: inline-flex;
}

.container {
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  position: relative;
  padding: var(--container-offset);

  display: flex;
  flex-direction: column;
  grid-gap: clamp(20px, 3.472vw, 50px);
}

* {
  &::-webkit-scrollbar {
    width: 5px;
    height: 7px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--color-a);
    transition: all .4s;
    border-radius: 10px;

    &:hover {
      background-color: var(--color-a);
    }
  }

  &::-webkit-scrollbar-track {
    background-color: rgba(50, 50, 50, 0.1);

    &:hover {
      background-color: rgba(50, 50, 50, 0.2);
    }
  }

  &::-webkit-scrollbar-button {
    background-color: transparent;
    box-shadow: none;
    display: none;
  }
}




